.contacts
  display: flex
  flex-direction: row

  &__el
    margin-left: 20px

    &:first-child
      margin-left: 0

  &__link
    position: relative
    color: $text
    +bold

    &:after
      position: absolute
      content: ''
      bottom: 0
      left: 50%
      transform: translateX(-50%)
      width: 0
      height: 2px
      background: $text
      transition: all .3s ease-in-out

    &:hover
      color: $text
      &:after
        width: 100%

    .icon
      top: 2px